import React, { ReactElement, useCallback, useState, createContext, useEffect } from 'react';
import { StaticContext } from 'react-router';
import { BrowserRouter as Router, Route, Switch, Redirect, RouteComponentProps } from 'react-router-dom';
import Tournament from './Tournament';
import './App.css';
import NotFound from './404';
import { socket, GET, cookies } from './utils/requests';
import Home from './Home';
import Header from './components/Header';
import { isMobile } from './utils/auth';
import { Login, Profile, Team } from './tournament/';
import { ProfileData } from './interfaces';

const sections = ['pairings', 'standings', 'stats'];
let lastTime = Date.now();
let lastSection = '';
let lastID = '';

function handleEnter(props: RouteComponentProps<any, StaticContext, any>) {
	if (props.history.action === 'PUSH') {
		if (socket.connected) socket.emit('public_page', {
			section: lastSection,
			tournament: lastID,
			time: (Date.now() - lastTime) / 60000,
			mobile: isMobile()
		});
	}
	let section = sections.find(s => props.location.pathname.includes(s)) || '';
	let id = props.match.params.id || '/';
	lastTime = Date.now();
	lastSection = section;
	lastID = id;
}

export const LoginContext = createContext(false);

const slide = ['/', 'pairings', 'standings', 'stats', 'tv', 'team', 'player', 'user', 'profile', 'login'];
function findIndex(pathname: string): number {
	for (let i = 0; i < slide.length; i++) {
		if (slide[i] === pathname) return i;
	}
	for (let i = slide.length; i > 0; i--) {
		if (pathname.includes(slide[i])) return i;
	}
	return 1;
}
function direction(_from: string, _to: string): string {
	let res = '';
	let from = findIndex(_from)
	let to = findIndex(_to);
	if (to > from) res = 'right';
	else if (to < from) res = 'left';
	let [nFrom, nTo] = [_from, _to].map(p => parseInt(p.split('/').pop() as string));
	if (nFrom && nTo && !isNaN(nFrom) && !isNaN(nTo)) {
		if (nTo > nFrom) res = 'right';
		else if (nFrom > nTo) res = 'left';
	}
	if (res && to >= 1 && to <= 4 && from >= 1 && to <= 4) res = 'small-' + res;
	return res;
}

interface State {
	direction: string
	referrer: string
	currentPath: string
}

let state: State = {
	direction: '',
	referrer: '',
	currentPath: ''
};

function App(): ReactElement {

	const [loggedIn, login] = useState(false);

	const [profile, setProfile] = useState({} as ProfileData);
	const updateProfile = useCallback(() => !loggedIn ? null : GET({ url: '/profile' })
		.then(setProfile)
		.then(() => login(true))
		.catch((e) => {
			console.error(e);
			login(false);
		}
	), [setProfile, login, loggedIn]);

	const pageLoad = useCallback(async () => {
		let sessionID = await cookies.get('sessionID');
		if (!sessionID) return;
		login(true);
	}, [login]);

	useEffect(() => {
		updateProfile();
		pageLoad();
	}, [pageLoad, updateProfile]);

  	return (
      	<Router basename={process.env.PUBLIC_URL}>
            <Route render={({location, history}) => {
				if (state.currentPath !== location.pathname) state = Object.assign(state || {}, {
					direction: direction(state.currentPath || location.pathname, location.pathname) || state.direction,
					referrer: state.currentPath || location.pathname,
					currentPath: location.pathname
				}) as State || {};
				location.state = state;
                return (
					<LoginContext.Provider value={loggedIn}>
						<Header setID={() => {}} profile={profile} referrer={state?.referrer} currentPath={location.pathname} />
						<Switch location={location}>

							<Route exact path='/' render={(props) => {
								handleEnter(props);
								return <Home {...props} />
							}} />
							<Route path='/profile' render={(props) => {
								return <Profile {...props} profile={profile} updateProfile={updateProfile} />
							}} />
							<Route path='/login' render={(props) => {
								return loggedIn ?
									<Redirect to={state.referrer} /> :
									<Login {...props} mode='login' login={login} updateProfile={updateProfile}/>;
							}} />
							<Route path='/team/:id/' render={(props) => {
								return <Team {...props} id={props.match.params.id} />
							}} />
							<Route path='/tournament/:id/' render={(props) => {
								return <Redirect to={props.location.pathname.slice(11)}/>
							}} />
							<Route path='/:id/' render={(props) => {
								handleEnter(props);
								return <Tournament {...props} />;
							}} />
							<Route path='*' component={NotFound} status={404} />
						</Switch>					
					</LoginContext.Provider>
                );
            }}/>
        </Router>
  	);
}

export default App;
